<template>
  <div class="deal-timer">
    <deal-timer class="deal-timer__item" v-if="!hide_counter" :special_to_date="special_to_date" />
    <div class="buyers-count text-center">
      <i class="fa fa-users" aria-hidden="true"></i>
      <span v-if="requiredPurchased">
        &nbsp;
        <b>{{ count }}</b>
        {{ $t('requiredPerchased') }}
      </span>
      <span v-else>
        &nbsp;
        <b>{{ purchased }}</b>
        {{ deal_bought_text || $t('dealsSold') }}
      </span>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "dealsSold": "tilboð seld",
    "requiredPerchased": "vantar til að virkja tilboð"
  },
  "en": {
    "dealsSold": "deals sold",
    "requiredPerchased": "required to activate the deal"
  }
}
</i18n>

<script>
import DealTimer from "@/components/DealTimer";

export default {
  name: "DealDetailsTimer",
  components: {
    DealTimer,
  },
  props: {
    deal_bought_text: String,
    target_number: Number,
    purchased: Number,
    hide_counter: Number,
    special_to_date: [Date, String],
  },
  data() {
    return {
      count: 0,
    };
  },
  computed: {
    requiredPurchased() {
      const { purchased, target_number } = this;
      if (purchased < target_number) {
        this.count = Math.abs(purchased - target_number);
        return true;
      }
      return false;
    },
  },
};
</script>

<style scoped>
.deal-timer__item + .buyers-count {
  padding-top: 10px;
}
.buyers-count {
  color: #989898;
  font-size: 14px;
  clear: both;
}
</style>
