import axios from "axios";
// Given a url string, call the url_rewrite webservice to obtain resource data (store, restaurant, product, etc.)
export default {
  name: "getPathData",
  props: {
    path: String,
    initialData: undefined,
  },
  methods: {
    async getPathData(value = this.path, query = "pathData") {
      let params = {
        service: "url_rewrite",
        query,
        value,
      };
      if (value.includes("verslun")) {
        params.cache = 600;
      }
      if (this.initialData) return this.initialData;

      const { data } = await axios.post("/ws.php", new URLSearchParams(params));

      return data;
    },
  },
};
