<template>
  <div class="product-sidebar-details">
    <deal-details-timer
      :deal_bought_text="deal_bought_text"
      :target_number="target_number"
      :hide_counter="hide_counter"
      :purchased="purchased"
      :special_to_date="special_to_date"
    />
    <div class="row">
      <div class="col-xs-4">
        <h6>{{ $t('fullPrice') }}</h6>
        {{ price | formatISK }}
      </div>
      <div class="col-xs-4">
        <h6>{{ $t('youSave') }}</h6>
        {{ price - special_price | formatISK }}
      </div>
      <div class="col-xs-4">
        <h6>{{ $t('discount') }}</h6>
        {{ discountPercentage }} %
      </div>
    </div>
    <base-button
      :disabled="addable"
      class="primary btn-block btn-add-deal"
      @click.native="addDealProductToCart()"
    >{{ $t('buy') }} | {{ special_price | formatISK }}</base-button>
  </div>
</template>

<i18n>
{
  "is": {
    "fullPrice": "Fullt verð",
