  "en": {
  "fullPrice": "Full Price",
  "youSave": "You Save",
  "discount": "Discount",
  "buy": "Buy"
  }
}
</i18n>

<script>
import CartItemMixin from "@/mixins/CartItemMixin";
import BaseButton from "@/components/BaseButton";
import DealDetailsTimer from "@/components/DealDetailsTimer";
import { mapGetters } from "vuex";

export default {
  name: "DealDetails",
  mixins: [CartItemMixin],
  components: {
    BaseButton,
    DealDetailsTimer,
  },
  props: {
    deal_bought_text: String,
    price: Number,
    target_number: Number,
    hide_counter: Number,
    special_price: Number,
    purchased: Number,
    special_to_date: [Date, String],
  },
  computed: {
    ...mapGetters(["loading"]),
    discountPercentage() {
      const { special_price, price } = this;
      return parseInt((1 - special_price / price) * 100);
    },
    isAvailable() {
      const eventDate = new Date(this.special_to_date);
      const currentDate = new Date();
      if (+eventDate < +currentDate) {
        return false;
      }
      return true;
    },
    addable() {
      const { adding, loading } = this;
      return adding || loading || !this.isAvailable;
    },
  },
  methods: {
    addDealProductToCart() {
      const { quantity, addToCart, increaseCartQty } = this;
      if (quantity > 0) {
        increaseCartQty();
      } else {
        addToCart();
      }
    },
  },
};
</script>

