<template>
  <div v-if="!deal" class="container mt--40 view-deal-container view-deal-container--app-ph">
    <div class="row">
      <div class="col-md-8 col-sm-6">
        <div class="view-deal__name app-ph" />
        <div class="view-deal__short-description app-ph" />
      </div>
    </div>
    <div class="row">
      <div class="col-md-8 col-sm-6">
        <div class="view-deal__img-wrapper app-ph"></div>
        <div class="description">
          <div class="description-title app-ph" />
          <div class="description-content app-ph" v-for="item in [1,2,3,4]" :key="item" />
        </div>
      </div>
      <div class="col-md-4 col-sm-6">
        <div class="view-deal__details app-ph" />
      </div>
    </div>
    <items-grid
      type="deal"
      title="Tilboð dagsins"
      sub-title="Sérvalið miðað við það sem aðrir eru að skoða núna"
      title-link-text="Skoða tilboð"
      :limit="90"
      class="item-deal--custom"
      useSkeleton
    />
  </div>
  <div v-else class="container mt--40 view-deal-container">
    <div class="row">
      <div class="col-md-8 col-sm-6">
        <h1 class="view-deal__name">{{ deal.name }}</h1>
        <p class="view-deal__short-description">{{ deal.short_description }}</p>
      </div>
    </div>
    <div class="row">
      <div class="col-md-8 col-sm-6">
        <div class="view-deal__img-wrapper">
          <img :src="mainImage" class="img img-responsive" :alt="deal.name" />
          <div
            v-if="deal.overlay_url"
            class="view-deal__img--overlay"
            :style="{ 'background-image': 'url(' + deal.overlay_url + ')' }"
          />
        </div>
        <!-- TODO: Implement image carousel for media gallery -->
        <div class="description">
          <h3>{{ $t("dealDescription") }}</h3>
          <div v-html="fixHtmlImages(deal.description)"></div>
        </div>
      </div>
      <div class="col-md-4 col-sm-6">
        <deal-details v-bind="deal" />
        <h5 class="subcontent-title">{{ $t("finePrint") }}</h5>
        <p v-html="deal.fineprint">{{ "deal.fineprint" }}</p>
        <p>
          Gildistími: {{ deal.coupon_valid_from | fixDateToIS }} -
          {{ deal.enjoyby | fixDateToIS }}
        </p>
        <h5 class="subcontent-title">{{ $t("dealVendor") }}</h5>
        <p v-html="deal.customersite">{{ deal.customersite }}</p>
        <!-- <h5>{{ $t('joinEmailList') }}</h5> -->
        <!-- TODO: Implement JSON webservice for https://www.aha.is/newsletter/subscriber/new/ -->
      </div>
    </div>
    <items-grid
      type="deal"
      title="Tilboð dagsins"
      sub-title="Sérvalið miðað við það sem aðrir eru að skoða núna"
      title-link-text="Skoða tilboð"
      :limit="90"
      class="item-deal--custom"
      useSkeleton
    />
  </div>
</template>

<i18n>
{
  "is": {
    "finePrint": "Smáa Letrið",
    "dealVendor": "Söluaðili",
    "joinEmailList": "Skráðu þig á póstlistann",
    "dealDescription": "Nánari Lýsing"
  },
  "en": {
    "finePrint": "The fine print",
    "dealVendor": "Seller info",
    "joinEmailList": "Join our e-mail list",
    "dealDescription": "Deal Information"
  }
}
</i18n>

<script>
import ItemsGrid from "@/components/ItemsGrid";
import DealDetails from "@/components/DealDetails";
import getPathData from "@/mixins/getPathData";
import { mapActions } from "vuex";
import get from "@/util/get";

export default {
  name: "ViewDeal",
  mixins: [getPathData],
  components: {
    ItemsGrid,
    DealDetails,
  },
  data() {
    return {
      deal: undefined,
    };
  },
  computed: {
    mainImage() {
      return `https://images.aha.is/image/upload/f_auto,w_1480,h_982/products/${this.deal.image}`;
    },
  },
  filters: {
    fixDateToIS(inDate) {
      var date = new Date(inDate);
      var options = { year: "numeric", month: "2-digit", day: "2-digit" };
      var _resultDate = new Intl.DateTimeFormat(options).format(
        date,
        "DD/MM/YYYY"
      );
      return _resultDate.replace(/\//g, ".");
    },
  },
  methods: {
    ...mapActions(["setNavBarTitle"]),
    fixHtmlImages(html) {
      return html
        .replace(/({{media url=")|("}})|/gi, "")
        .replace(
          /(https:\/\/www\.aha\.is\/media\/wysiwyg\/)|(\/media\/wysiwyg\/)/g,
          "https://images.aha.is/image/upload/wysiwyg/"
        )
        .replace(/("wysiwyg)/g, '"https://images.aha.is/image/upload/wysiwyg')
        .replace(/https:https:/g, "https:");
    },
  },
  watch: {
    async $route() {
      this.deal = null;
      const data = await this.getPathData();
      this.deal = data[0][0];
    },
  },
  async mounted() {
    const data = await this.getPathData();
    this.deal = data[0][0];
    this.setNavBarTitle({
      en: get(this.deal, "en_name", ""),
      is: get(this.deal, "is_name", ""),
    });
  },
  beforeRouteUpdate(to, _, next) {
    window.scrollTo(0, 0);
    next();
  },
};
</script>

